<template>
  <div>
    <div v-if="isLoaderIsActive">
      <loader>
        <h3 slot="message">Loading...</h3>
      </loader>
    </div>
    <div v-else>
      <h1>Welcome! {{ currentUserName }}</h1>
      <v-divider></v-divider>
      <p>Select the date for which you wanted to fetch astronomical picture of the day.</p>
      <v-date-picker
        v-model="selectedDate"
        full-width
        :max="currentDate"
        min="1995-06-20"
        class="mt-4" />
        <v-btn block color="primary" @click="fetchApiData()">
          Click Here to Fetch Data
        </v-btn>
    </div>
  </div>
</template>
<script>
import { eventBus } from '../../main'
import Loader from '../Widgets/Loader'
export default {
  name: 'Home',
  components: {
    loader: Loader
  },
  data: () => ({
    fetchedUsersData: [],
    currentUserID: null,
    usersDataAPI:
      'https://vue-training-3f5e1-default-rtdb.firebaseio.com/data.json',
    currentUserName: '',
    isLoaderIsActive: true,
    search: '',
    selectedDate: null,
    currentDate: null
  }),
  created () {
    this.currentDate = new Date().toISOString().substr(0, 10)
    this.selectedDate = this.currentDate
    eventBus.$emit('isUserSessionActive', true)
    this.axios
      .get(this.usersDataAPI)
      .then(response => {
        const fetchedUsersData = []
        for (const key in response.data) {
          fetchedUsersData.push(response.data[key])
        }
        return fetchedUsersData
      })
      .then(processedData => {
        this.fetchedUsersData = processedData
        this.currentUserID = parseInt(localStorage.getItem('UserID'))
        this.currentUserName = this.fetchedUsersData[this.currentUserID].name
        this.isLoaderIsActive = false
      })
      .catch(err => {
        console.log(err)
      })
  },
  beforeRouteEnter (from, to, next) {
    !localStorage.getItem('UserID') ? next('/home') : next(true)
  },
  computed: {
    headers () {
      return [
        {
          text: 'Name',
          align: 'start',
          value: 'name'
        },
        { text: 'Emaill', value: 'email' },
        { text: 'Language', value: 'language' }
      ]
    }
  },
  methods: {
    fetchApiData () {
      this.$router.push('/apod/' + this.selectedDate)
    },
    filterText (value, search) {
      return (
        value !== null &&
        search !== null &&
        typeof value === 'string' &&
        value
          .toString()
          .toLowerCase()
          .includes(search.toLowerCase())
      )
    }
  }
}
</script>
<style scoped>
div h1, p {
  text-align: center;
  margin-top: 1%;
  text-transform: uppercase;
}
</style>
